<template>
  <div>
    <h3>{{ coinName }} Price Chart (Last 7 Days)</h3>
    <LineChart :chart-data="chartData" v-if="chartData.labels.length" />
  </div>
</template>

<script>
import { Line } from "vue-chartjs";
import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  LineElement,
  PointElement,
  CategoryScale,
  LinearScale,
} from "chart.js";

ChartJS.register(Title, Tooltip, Legend, LineElement, PointElement, CategoryScale, LinearScale);

export default {
  components: { LineChart: Line },
  props: {
    coinId: String,
    coinName: String,
  },
  data() {
    return {
      chartData: {
        labels: [],
        datasets: [{ label: "Price", data: [], borderColor: "#42A5F5", fill: false }],
      },
    };
  },
  watch: {
    coinId: "fetchChartData",
  },
  async mounted() {
    if (this.coinId) {
      await this.fetchChartData();
    }
  },
  methods: {
    async fetchChartData() {
      try {
        const response = await fetch(
          `https://api.coingecko.com/api/v3/coins/${this.coinId}/market_chart?vs_currency=usd&days=7`
        );
        const data = await response.json();
        
        this.chartData = {
          labels: data.prices.map((entry) => new Date(entry[0]).toLocaleDateString()),
          datasets: [
            {
              label: `${this.coinName} Price (USD)`,
              data: data.prices.map((entry) => entry[1]),
              borderColor: "#42A5F5",
              fill: false,
            },
          ],
        };
      } catch (error) {
        console.error("Error fetching chart data:", error);
      }
    },
  },
};
</script>
